import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { Message } from "primeng/primeng";
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import * as $ from 'jquery';
import * as moment from 'moment';
import * as _ from 'lodash';
import { Commfunc } from '../../../../../../@uisftech/common/service/commonFun';
import { Equhundered } from '../../../../../../@uisftech/common/service/validator';
import { MarketingHttpService } from '../../http/marketing.http.service';
/**
 * 营销要求
 */
@Component({
	selector: 'marketing-require',
	templateUrl: './marketing-require.component.html',
	styleUrls: ['./marketing-require.component.scss'],
	providers: [MarketingHttpService]
})
export class MarketingRequireComponent implements OnInit {
	tabMesg: string = tableMessage;
	msgs: Message[] = []
	display: boolean = false;
	upInValue: any;
	dataList: any = [];
	requestList: any = [];
	marketingCampaignId: string;
	ch: any = CALENDAR_CN;
	fromDate: Date = null;
	marketingCampaigntType: string;
	activiteData: any = {};//接收详情数据
	flagCopy: string;
	url: any = [];
	isNext: boolean = false;
	deId: string;
	showDisabled: boolean = false;
	constructor(
		public router: Router,
		public routeInfo: ActivatedRoute,
		public commfunc: Commfunc,
		public marketingHttpService: MarketingHttpService,
	) {

	}
	ngOnInit() {
		setTimeout(() => {
			this.requestList = [];
		})
		this.marketingCampaignId = this.routeInfo.snapshot.params['marketingCampaignId'];
		this.marketingCampaigntType = this.routeInfo.snapshot.params['marketingCampaigntType'];
		this.fromDate = new Date(this.commfunc.getSessionDataCH('fromDate'));
		// this.deId=this.routeInfo.snapshot.params['deId'];
		this.url = JSON.parse(this.commfunc.getSessionDataCH('markingURL'));
		this.url.forEach((element, index) => {
			if (element.label == "MarketingRequireComponent") {
				if (index + 1 == this.url.length) {//判断是最后一个
					this.isNext = true;
				}
			}
		});
		if (this.marketingCampaignId) {
			this.echoClick()
		}
		this.flagCopy = this.routeInfo.snapshot.params['flagCopy'];//复制和修改的标识
		// if (this.flagCopy) {
		// 	this.activiteData = JSON.parse(this.routeInfo.snapshot.params['activiteData']);//接收详情的数据
		// 	if (this.activiteData.requireList) {
		// 		this.requestList = this.activiteData.requireList;
		// 		console.log(this.requestList)
		// 		this.requestList.forEach((element, i) => {
		// 			if (element.marketingChannelTimeFrom) {
		// 				this.requestList[i].endtime = new Date(element.endtime);
		// 			}
		// 		});
		// 	}
		// 	if (this.activiteData.statusId == '3') {//状态为已发布进行中时不给修改
		// 		this.showDisabled = true;
		// 	} else {
		// 		this.showDisabled = false;
		// 	}
		// }
		if (this.routeInfo.snapshot.params['relationProductList']) {
			this.dataList = JSON.parse(this.routeInfo.snapshot.params['relationProductList']);
		}
	}
	//新增
	doAdd() {
		let aa = _.clone(this.requestList);
		aa.push({
			endtime: '', disposeScheduleRequire: '', marketingSucRate: ''
		});
		this.requestList = aa;
	}
	//删除
	toDelete(i) {
		this.requestList.splice(i, 1);
		this.requestList = _.clone(this.requestList);
	}
	onKeyupPercent(event) {
		if (Equhundered(null, event.target.value)) {
			this.msgs = [{ severity: 'error', summary: '提示', detail: '请输入正确的营销成功率' }];
			event.target.value = '';
		}
	}
	onKeyupPercent1(event) {
		if (Equhundered(null, event.target.value)) {
			this.msgs = [{ severity: 'error', summary: '提示', detail: '请输入正确的处理进度要求' }];
			event.target.value = '';
		}
	}
	//上一步
	prev() {
		let param = {};
		if (this.flagCopy) {
			param = {
				marketingCampaignId: this.marketingCampaignId,
				marketingCampaigntType: this.marketingCampaigntType,
				relationProductList: JSON.stringify(this.dataList),
				flagCopy: this.flagCopy,
				tabPrev:'tabUp'
			}
		} else {
			param = {
				marketingCampaignId: this.marketingCampaignId,
				marketingCampaigntType: this.marketingCampaigntType,
				relationProductList: JSON.stringify(this.dataList),
				tabPrev:'tabUp'
			}
		}
		this.url = JSON.parse(this.commfunc.getSessionDataCH('markingURL'));
		let navUrl = '';
		let indexId = '';
		this.url.forEach((element, index) => {
			if (element.label == "MarketingRequireComponent") {
				navUrl = this.url[index - 1].url;
				indexId = this.url[index - 1].index;
			}
		});
		this.router.navigate([navUrl, param]);//营销渠道
		this.commfunc.setSessionDataCH('index', indexId)
	}
	//下一步
	next() {
		let param = {};
		if (this.flagCopy) {
			param = {
				marketingCampaignId: this.marketingCampaignId,
				marketingCampaigntType: this.marketingCampaigntType,
				relationProductList: JSON.stringify(this.dataList),
				flagCopy: this.flagCopy,
			}
		} else {
			param = {
				marketingCampaignId: this.marketingCampaignId,
				marketingCampaigntType: this.marketingCampaigntType,
				relationProductList: JSON.stringify(this.dataList),
			}
		}
		// if (this.requestList.length > 0) {//有数据则调接口
			for (let ind of this.requestList) {
				if (ind['endtime'] == '') {
					this.msgs = [];
					this.msgs.push({ severity: 'error', summary: '提示', detail: ' 截至时间不能为空！' });
					return;
				}
				if (ind['disposeScheduleRequire'] == '') {
					this.msgs = [];
					this.msgs.push({ severity: 'error', summary: '提示', detail: '进度要求不能为空！' });
					return;
				}
				if (ind['marketingSucRate'] == '') {
					this.msgs = [];
					this.msgs.push({ severity: 'error', summary: '提示', detail: '营销成功率不能为空！' });
					return;
				}
			}
			this.requestList.forEach((element, i) => {
				this.requestList[i].endtime = moment(element.endtime).format("YYYY-MM-DD");
			});
			let param1 = {
				marketingCampaignId: this.marketingCampaignId,
				requireList: this.requestList,
			}
			this.marketingHttpService.createCampaignRequire(param1).subscribe(data => {
				if (data.returnCode.code == 'success') {
					let paramSum = {
						marketingCampaignId: this.marketingCampaignId,
						type: 'marking',
						marketingCampaigntType: this.marketingCampaigntType,
					}
					if (this.isNext) {
						this.router.navigate(['/pages/tzb/custom/marketing/marketing-activite/marketing-activity-detail', paramSum])
					} else {
						this.url = JSON.parse(this.commfunc.getSessionDataCH('markingURL'));
						let navUrl = '';
						let indexId = '';
						this.url.forEach((element, index) => {
							if (element.label == "MarketingRequireComponent") {
								navUrl = this.url[index + 1].url;
								indexId = this.url[index + 1].index;
							}
						});
						this.router.navigate([navUrl, param]);
						this.commfunc.setSessionDataCH('index', indexId)

					}
					this.msgs = [];
					this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
				} else {
					this.msgs = [];
					this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
				}
			}, error => {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
			})
		// } 
		// else {//没数据直接跳下一步
		// 	let paramSum = {
		// 		marketingCampaignId: this.marketingCampaignId,
		// 		type: 'marking',
		// 		marketingCampaigntType: this.marketingCampaigntType,
		// 	}
		// 	if (this.isNext) {
		// 		this.router.navigate(['/pages/tzb/custom/marketing/marketing-activite/marketing-activity-detail', paramSum])
		// 	} else {
		// 		this.url = JSON.parse(this.commfunc.getSessionDataCH('markingURL'));
		// 		let navUrl = '';
		// 		let indexId = '';
		// 		this.url.forEach((element, index) => {
		// 			if (element.label == "MarketingRequireComponent") {
		// 				navUrl = this.url[index + 1].url;
		// 				indexId = this.url[index + 1].index;
		// 			}
		// 		});
		// 		this.router.navigate([navUrl, param]);
		// 		this.commfunc.setSessionDataCH('index', indexId)

		// 	}
		// }

	}

	//重置
	rest() {
		this.requestList.forEach(element => {
			element.endtime = '';
			element.disposeScheduleRequire = '';
			element.marketingSucRate = '';
		});
	}
	//详情
	echoClick() {
		this.marketingHttpService.queryMarketingCampaignById({ marketingCampaignId: this.marketingCampaignId }).subscribe(data => {
			if (data.returnCode.code == 'success') {
				let marketingParam ={
					marketingObjectType:data.marketingObjectType,
					exploreCondtionId:data.exploreCondtionId
				}
				this.commfunc.setSessionDataCH('marketingParam',JSON.stringify(marketingParam));
				if (data.requireList) {
					this.requestList = data.requireList;
					console.log(this.requestList)
					this.requestList.forEach((element, i) => {
						if (element.marketingChannelTimeFrom) {
							this.requestList[i].endtime = new Date(element.endtime);
						}
					});
				}
				if (data.statusId == '3') {//状态为已发布进行中时不给修改
					this.showDisabled = true;
				} else {
					this.showDisabled = false;
				}
			}
		})
	}
} 
